export default "#bisection-container{\n    background-color: black;\n    min-height: 70vh;\n    padding: 2rem;\n    \n}\n\n.biform{\n    display: grid;\n    grid-template-columns: repeat(3,1fr);\n    gap: 1rem;\n}\n\n.biform__submit{\n    grid-column: 3/4;   \n    background-color: green;\n    border: none;\n    color: inherit;\n    border-radius: 5px;\n}\n\n.biform__submit:hover{\n    background-color: rgba(0, 128, 0, 0.5);\n}\n\n.biform__submit:focus{\n    border: 1px solid greenyellow;\n    outline: none;\n}\n\n.biform__submit::-moz-focus-inner {\n    border: 0;\n}\n\n.biform__label{\n    grid-column: 1/1;\n}\n\n.biform__input{\n    grid-column: 2/4;\n}\n\n.biform > *[type=number]:focus,*[type=text]:focus,#precision:focus{\n    border: 2px solid rgb(0, 89, 255);\n    border-radius: 2px;\n}\n\n.bidisplay{\n    margin-top: 3rem;\n}\n\n.bidisplay__table{\n    max-width: 700px;\n    margin: 0 auto;\n}\n\n@media only screen and (max-width:600px){\n    .biform{\n        grid-template-columns: 1fr;\n    }\n    .biform > *{\n        grid-column: auto;\n    }\n}\n/*# sourceMappingURL=data:application/json;base64, */"